<template>
  <div>
    <h1 class="title">{{ msg }}</h1>
    <label>
      <input type="text" class="in-word" v-model="inputString" @input="findanagram" placeholder="Enter a word">
    </label>
<!--    <button class="generate" @click="findanagram">Generate Anagram</button>-->
    <div class="container" v-if="outputAnagrams.length">
      <div class="outAna-container" v-for="(ana) in outputAnagrams" >
        {{ ana }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Anagram Generator',
      inputString: '',
      outputAnagrams: [],
    }
  },
  methods: {
    findanagram() {
      const path = "http://127.0.0.1:5000/api/v1/findAnagram"
      // const path = "http://backend:5000/api/v1/findAnagram"
      const inputWord = {
        "word": this.inputString
      }
      axios
        .post(path,inputWord)
        .then((response) => {
          this.outputAnagrams = response.data.anagrams;
          console.log(response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .title {
    display: inline-block;
    margin: 5% 35% 2% 35%;
    font-family: "Songti TC";
    letter-spacing: 2px;
  }

  .in-word {
    display: block;
    margin-left: 37%;
    width: 20%;
    height: 50px;
    outline-style: none;
    font-size: 25px;
    padding-left: 10px;
    font-family: "Al Bayan";
    border: none;
    background-color: transparent;
    border-bottom: 2px solid;
  }
  .generate {
    display: block;
    margin-top: 2%;
    margin-left: 43%;
    padding: 10px;
    background-color: #0b6639;
    border-color: transparent;
    border-radius: 25px;
    color: whitesmoke;
    font-size: 0.9rem;
    outline-style: none;
    cursor: pointer;
    font-family: Seravek;
    letter-spacing: 1px;
  }

  .generate:hover {
    background-color: #7409c6;
  }

  .container {
    margin: 3% 30%;
  }

  .outAna-container {
    display: inline-block;
    margin: 15px;
    width: auto;
    color: whitesmoke;
    letter-spacing: 2px;
    font-family: "Al Bayan";
    text-align: center;
    height: auto;
    padding: 15px;
    border-radius: 15px;
    background-color: #033988;
  }
</style>



