import Vue from 'vue'
import Router from 'vue-router'
import Angaram from '@/components/Anagram'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Anagram',
      component: Angaram
    }
  ]
})



